
.container-fluid, .row, .sidebar, .main {
    height: 100%;
    overflow-y: hidden;
}

.sidebar {
    background: #f5f5f5;
    padding-top: 20px;
}

.main {
    background-color: #428bca;
    color: #fff;
}

.blink {
    -webkit-animation: blinker 1s linear infinite;
            animation: blinker 1s linear infinite;
}

@-webkit-keyframes blinker {
    50% {
        opacity: 0;
    }
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}





